/* ////========== HIGHLEVEL WIDGET STYLES =========//// */

* {
  box-sizing: border-box!important; }

html,
body {
  padding: 0;
  margin: 0; }

body {
  font-family: Roboto, system, -apple-system, BlinkMacSystemFont, ".SFNSDisplay-Regular", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #607179;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a,
button {
  transition: all .2s ease-in-out;
  cursor: pointer;
  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
    outline: none; }
  &:active {
    &:focus {
      outline: none; } } }

a[x-apple-data-detectors] {
  color: inherit!important;
  text-decoration: none!important;
  font-size: inherit!important;
  font-family: inherit!important;
  font-weight: inherit!important;
  line-height: inherit!important; }

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #2a3135;
  font-weight: normal;
  margin-top: 0; }

p {
  margin-top: 0;
  margin-bottom: 0;
  + p {
    margin-top: 15px; } }

.hl_widget--card {
  background: #fff;
  color: #607179;
  font-family: Roboto, system, -apple-system, BlinkMacSystemFont, ".SFNSDisplay-Regular", "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: relative;
  padding-bottom: 30px;
  .powered {
    font-size: 12px;
    position: absolute;
    bottom: 20px;
    right: 30px;
    color: #afb8bc;
    em {
      font-style: normal;
      color: #607179; } }
  //header
  &-header {
    padding: 40px 50px 20px 50px;
    border-bottom: 2px solid #f2f7fa;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: 767px) {
      padding: 30px 30px 10px 30px; }
    > div {
      flex: 1 0 50%;
      margin-bottom: 10px; }
    .hl_widget--card-header-left {
      padding-right: 30px; }
    .hl_widget--card-header-right {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start; }
    h2 {
      font-size: 32px;
      font-weight: 300;
      line-height: 1.2;
      color: #2a3135;
      margin-bottom: 0;
      @media (max-width: 767px) {
        font-size: 24px; } }
    h3 {
      font-size: 50px;
      font-weight: 300;
      line-height: 1;
      margin-bottom: 0;
      vertical-align: top;
      margin-right: 30px;
      @media (max-width: 767px) {
        font-size: 40px; }
      .rating {
        display: inline-block;
        position: relative;
        top: 5px;
        > div {
          margin-left: -8px;
          svg {
            vertical-align: top; } } } } }
  //body
  &-body {
    padding: 50px;
    @media (max-width: 767px) {
      padding: 30px; }
    .hl_widget--card-carousel-item {
      &:focus {
        outline: none; } } } }

.hl_widget--inner {
  &.--two-columns {
    @media (min-width: 768px) {
      margin-left: -15px;
      margin-right: -15px;
      &:before,
      &:after {
        content: "";
        display: table; }
      &:after {
        clear: both; }
      .hl_widget--inner-item {
        float: left;
        width: 50%;
        padding-left: 15px;
        padding-right: 15px; } } }
  &.--three-columns {
    @media (min-width: 768px) {
      margin-left: -15px;
      margin-right: -15px;
      &:before,
      &:after {
        content: "";
        display: table; }
      &:after {
        clear: both; }
      .hl_widget--inner-item {
        float: left;
        width: 33.33333%;
        padding-left: 15px;
        padding-right: 15px; } } }
  &-item {
    margin-bottom: 30px;
    &-header {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
      > * {
        margin-bottom: 5px; }
      h2 {
        font-size: 16px;
        line-height: 1;
        color: #263340;
        margin-right: 20px;
        span {
          font-size: 14px;
          color: #607179;
          display: inline-block;
          margin-left: 10px;
          line-height: 16px; } }
      .rating {
        > div {
          margin-left: -4px;
          svg {
            max-width: 14px;
            height: auto; } } } } } }

.rating {
  > div {
    display: inline-block;
    width: 18px; } }

.hl_widget--card-body {
  &.slick-slider {
    .slick-dots {
      list-style: none;
      text-align: center;
      padding: 20px 0;
      margin: 0;
      li {
        display: inline-block;
        line-height: 1;
        margin: 0 10px;
        button {
          border: none;
          padding: 0;
          font-size: 0;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: rgba(24, 139, 246, 0.2); }
        &.slick-active {
          button {
            background-color: #188bf6; } } } } } }

.hl_widget--nav {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: absolute;
  bottom: 65px;
  transition: all .2s ease-in-out;
  @media (min-width: 768px) {
    width: 53px;
    height: 53px;
    bottom: 80px; }
  &.--prev {
    left: 30px;
    @media (min-width: 768px) {
      left: 50px; } }
  &.--next {
    right: 30px;
    @media (min-width: 768px) {
      right: 50px; } }
  &:hover,
  &:focus,
  &:active {
    background-color: rgba(24, 139, 246, 0.2); }
  svg {
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; } }

